.styles_input__bJkBB {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
  appearance: none;
  background: #232e3c;
  border: none;
  border-radius: 12px;
  color: #f5f5f5;
  display: block;
  // padding: 12px 0;
  padding: 8px 38px 8px 10px;
  width: calc(100% - 78px);
  outline: none;
}
.styles_input__bJkBB.styles_iconIncluded__2srZt {
  padding-left: 38px;
  font-size: 16px;
  line-height: 28px;
}

.style_actionHolder__grO6j {
  color: #708499;
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 8px;
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
}

.styles_root__aDhZI {
  display: inline-block;
  position: relative;
  width: 100%;
}

.styles_icon__tlXf7 {
  color: #708499;
  left: 8px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  z-index: 1;
}

.trade_textContainer {
  justify-content: center;
  display: flex;
  align-items: center;
  padding: 0 12px;
}



.trade_title {
  font-family: var(--font-display-title);
  font-size: 48px;
  text-align: center;
  margin-bottom: 16px;
  line-height: 48px;
  font-weight: 700;
  display: inline-block;
  vertical-align: middle;
  width: auto;
  color: #fff;
}

