.trade_skeleton {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
  // padding: 20px 0;
  list-style: none;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  background-color: transparent;
  &__wrapper {
    width: 100%;
    height: 50px;
    position: relative;
  }
}

// circle
.trade_skeleton__circle {
  position: absolute;
  border-radius: 100%;
  // left: 20px;
  width: 48px;
  height: 48px;
  background-color: rgba(255, 255, 255, 0.04);
  display: flex;
  justify-content: center;
  align-items: center;
}

